<template>
  <div>
    <el-row type="flex">
      <el-menu :router="true" default-active="/homes/goods" class="tab-item" mode="horizontal">
        <el-col :span="8">
          <el-menu-item index="/homes/goods" class="title">商品</el-menu-item>
        </el-col>
        <el-col :span="8">
          <el-menu-item index="/homes/ratings" class="title">评论</el-menu-item>
        </el-col>
        <el-col :span="8">
          <el-menu-item index="/homes/seller" class="title">商家</el-menu-item>
        </el-col>
      </el-menu>
    </el-row>
    <router-view :seller="seller"></router-view>
  </div>
</template>
<script>
export default {
  props: ['seller']
}

</script>
<style lang="less">
.el-menu {
  background-color: transparent;
}

.el-menu-item.is-active {
  color: #778593;
}

.el-menu-item:focus,
.el-menu-item:hover {
  background-color: transparent;
}

.tab-item {
  width: 100%;
  text-align: center;

  .title {
    height: 40px;
    line-height: 40px;
  }
}

</style>



